import React from 'react';
import SectionPaper from '../components/SectionPaper';
import ExpData from '../data/experience.json';
import styles from './styles/experience.module.css';

const ExperienceSection = () => {
  return (
    <SectionPaper title="Experiences">
      <div>
        {ExpData.companies.map((exp, i) => {
          return (
            <div key={`exp-${i}`} className={styles.expitem}>
              <img
                alt="logo"
                className={styles.logo}
                src={
                  exp.logo.includes('http')
                    ? exp.logo
                    : process.env.PUBLIC_URL + exp.logo
                }
              />
              <div className={styles.expcontent}>
                <h3 className={`${styles.position} ${styles.itemtext}`}>
                  {exp.position}
                </h3>
                <h4 className={`${styles.name} ${styles.itemtext}`}>
                  {exp.name}
                </h4>
                <span className={`${styles.duration} ${styles.itemtext}`}>
                  {exp.duration}
                </span>
              </div>
            </div>
          );
        })}
      </div>
    </SectionPaper>
  );
};

export default ExperienceSection;
