import React, { useEffect, useState } from 'react';
import { useFrame } from '@react-three/fiber';
import * as THREE from 'three';
import { useGLTF } from '@react-three/drei';
import WalkerModel from '../assets/models/at-at.glb';
import { GLTF } from 'three/examples/jsm/loaders/GLTFLoader';

const Walker = () => {
  const { scene, animations } = useGLTF(WalkerModel) as GLTF;

  const [mixer, setMixer] = useState<THREE.AnimationMixer | null>(null);
  const [action, setAction] = useState<THREE.AnimationAction | null>(null);
  const [direction, setDirection] = useState<number[]>([0, 0]);

  useEffect(() => {
    const action = mixer?.clipAction(animations[0]);

    if (action) {
      setAction(action);
    }
  }, [setAction, animations, mixer]);

  useEffect(() => {
    scene.scale.set(0.01, 0.01, 0.01);
    scene.position.set(0.6, 2.05, 1);
    setMixer(new THREE.AnimationMixer(scene));
  }, [scene]);

  useEffect(() => {
    const keyHandle = (e: KeyboardEvent) => {
      const dir = [0, 0];
      switch (e.code) {
        case 'KeyA':
          dir[0] = 1;
          break;
        case 'KeyD':
          dir[0] = -1;
          break;
        case 'KeyW':
          dir[1] = -1;
          break;
        case 'KeyS':
          dir[1] = 1;
          break;
      }
      action?.play();
      setDirection(dir);
    };

    const onKeyUp = () => {
      action?.stop();
      setDirection([0, 0]);
    };
    document.addEventListener('keydown', keyHandle);
    document.addEventListener('keyup', onKeyUp);
    return () => {
      document.removeEventListener('keydown', keyHandle);
      document.removeEventListener('keyup', onKeyUp);
    };
  }, [scene, action]);

  useFrame((state, delta) => {
    const speed = 0.05;
    const pos = scene.position;
    mixer?.update(delta);
    scene.position.set(
      pos.x + speed * direction[1] * delta,
      pos.y,
      pos.z + speed * direction[0] * delta
    );
    if (direction[0] !== 0 || direction[1] !== 0) {
      scene.lookAt(
        new THREE.Vector3(pos.x + direction[1], pos.y, pos.z + direction[0])
      );
    }
  });

  return <primitive object={scene} />;
};

export default Walker;
