import React, { useState } from 'react';
import { useFrame } from '@react-three/fiber';
import { useGLTF } from '@react-three/drei';
import DeskModel from '../assets/models/desk.glb';
import { GLTF } from 'three/examples/jsm/loaders/GLTFLoader';

type DeskProps = {
  angle: number;
};

const Desk = ({ angle }: DeskProps) => {
  const { scene } = useGLTF(DeskModel) as GLTF;
  const [currAngle, setCurrAngle] = useState<number>(0);

  useFrame((state) => {
    setCurrAngle((prev) => {
      return prev + 20 * (angle - prev) * state.clock.getDelta();
    });
    state.camera.position.set(
      3 * Math.cos(2 * Math.PI * currAngle),
      4.2,
      3 * Math.sin(2 * Math.PI * currAngle)
    );
    state.camera.lookAt(0, 1, 0);
  });

  return <primitive object={scene} />;
};

export default Desk;
